type SourceImage = {
  id: string
  hd: string
  imageUrls?: Array<string> | null
  name: string
}

const renameDuplicatedSources = (sources: Array<SourceImage>) => {
  const sourcesMap = new Map()
  const renamedSources = sources.map(source => {
    const name = source.name
    const sourceName = sourcesMap.get(name)
    if (sourceName) {
      sourcesMap.set(name, sourcesMap.get(name) + 1)
      const newSourceName = `${name} (${sourcesMap.get(name)})`
      return { ...source, srcName: newSourceName }
    } else {
      sourcesMap.set(name, 1)
      return { ...source, srcName: name }
    }
  })
  return renamedSources
}

export { renameDuplicatedSources }
